@import "../../../../vars.scss";

.slide {
  position: relative;
  height: 100%;
  display: none;
  width: 100%;
  &.active {
    display: block;
    transition: 0.2s all;
  }
  &__image {
    transition: 0.2s all;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;

    z-index: 3;
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(to top, #000, transparent);
    }
    img {
      display: block;
      top: 0;
      bottom: 0;
      position: absolute;
      z-index: -1;
      min-width: 100%;
    }
  }
  &__wrapper {
    display: flex;
    position: relative;
    z-index: 5;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #fff;
  }
  &__content {
    flex: 1 1;
    padding: 0 32px 0 32px;
  }
  &__title {
    font-size: 32px;
    font-weight: 500;
    margin-bottom: 8px;
  }
  &__text {
    font-weight: 300;
    font-size: 22px;
    line-height: 22px;
    margin-bottom: 32px;
  }
  &__button {
    width: 164px;
    height: 48px;
    border-radius: 5px;
    color: #fff;
    font-size: 18px;
    line-height: 21px;
  }
  &__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 100%;
    cursor: pointer;
    position: relative;
    transition: 0.2s all;
    &:hover {
      background-color: rgba(14, 194, 97, 0.2);
    }
  }
}

@media (min-width: 1440px) {
  .slide {
    &__arrow {
      width: 64px;
    }
    &__title {
      font-size: 40px;
    }
    &__text {
      font-size: 24px;
    }
  }
}
