.slider {
  grid-area: slider;
  display: none;
  max-height: 100%;
  position: relative;
  max-width: 100%;
  
  z-index: 2;
  &.hide::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(21,27,31,0.8);
    z-index: 10;
  }
}

@media (min-width: 1024px) {
  .slider {
    width: 410px;
    flex: 1 1;
    display: flex;
    min-height: 100%;
  }
}
